import {
  LOAD_DEVICES,
  LOAD_DEVICES_SUCCESS,
  LOAD_MAPPINGS,
  LOAD_MAPPINGS_SUCCESS,
  ADD_RASP_TO_USER,
  ADD_RASP_TO_USER_SUCCESS,
  DELETE_MAPPING,
  DELETE_MAPPING_SUCCESS,
  LOAD_SESSION_LOGS,
  LOAD_SESSION_LOGS_SUCCESS,
} from "../actionTypes/devices";

const initialState = {
  devices: [],
  mappings: [],
  sessionData: undefined,
};

export default function devicesReducer(state = initialState, action = {}) {
  switch (action.type) {
    case LOAD_SESSION_LOGS: {
      return { ...state };
    }
    case LOAD_SESSION_LOGS_SUCCESS: {
      return { ...state, sessionData: action.sessionData };
    }
    case ADD_RASP_TO_USER: {
      return { ...state };
    }
    case ADD_RASP_TO_USER_SUCCESS: {
      return { ...state };
    }
    case DELETE_MAPPING: {
      return { ...state };
    }
    case DELETE_MAPPING_SUCCESS: {
      return {
        ...state,
        mappings: state.mappings.filter(
          (item) => item.id !== action.mapping.id
        ),
      };
    }
    case LOAD_MAPPINGS: {
      return { ...state };
    }
    case LOAD_MAPPINGS_SUCCESS: {
      return { ...state, mappings: action.mappings };
    }
    case LOAD_DEVICES: {
      return { ...state };
    }
    case LOAD_DEVICES_SUCCESS: {
      return { ...state, devices: action.devices };
    }
    default: {
      return state;
    }
  }
}
