import { Button } from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";
import PropTypes from "prop-types";
import React from "react";
import {
  makeStyles,
  FormControl,
  Select,
  InputLabel,
  TextField,
  MenuItem,
} from "@material-ui/core";
import { closePopup } from "../../state/actions/popups";
import { addRaspberryToUser, deleteMapping } from "../../state/actions/devices";
import { loadMappings } from "../../state/actions/devices";

const useStyles = makeStyles({
  popupbox: {
    position: "absolute",
    background: "#00000050",
    width: "100%",
    height: "100vh",
    top: 0,
    left: 0,
  },
  box: {
    position: "relative",
    width: "70%",
    margin: "0 auto",
    height: "auto",
    maxHeight: "70vh",
    marginTop: "calc(15vh - 20px)",
    background: "white",
    borderRadius: "4px",
    padding: "20px",
    border: "1px solid black",
    overflow: "auto",
  },
  boxik: {
    display: "flex",
    color: "black",
    background: "white",
    fontSize: "1em",
    whiteSpace: "wrap",
    textTransform: "none",
    borderRadius: "5px",
    borderColor: "black",
    borderWidth: "1px",
    margin: "10px 10px",
    borderStyle: "solid",
    padding: "5px",
  },
  columns: {
    display: "flex",
  },
  closeicon: {
    content: "x",
    cursor: "pointer",
    position: "fixed",
    right: "calc(15% - 12.5px)",
    top: "calc(15vh - 32.5px)",
    background: "white",
    width: "25px",
    height: "25px",
    borderRadius: "50%",
    lineHeight: "20px",
    textAlign: "center",
    border: "1px solid black",
    fontSize: "20px",
    fontWeight: "bold",
  },
  makeEmptyButton: {
    border: "1px solid black",
    color: "white",
    background: "blue",
    marginTop: "10px",
    marginRight: "20px",
  },
  button: {
    border: "1px solid black",
    background: "green",
    color: "white",
    marginTop: "10px",
  },
  formControl: {
    display: "flex",
    marginBottom: "10px",
    minWidth: 200,
  },
  header: {
    margin: "10px 0px",
  },
  deleteButton: {
    marginTop: "10px",
    background: "red",
    color: "white",
    padding: "10px 20px",
    fontWeight: "bold",
    marginLeft: "auto",
    fontSize: "30px",
    "&:hover": {
      background: "black",
    },
  },
});

const MapRaspberryPopup = () => {
  const [didGet, setDidGet] = React.useState(false);
  const [shouldUpdate, setShouldUpdate] = React.useState(false);
  const [selectedUser, setSelectedUser] = React.useState(undefined);
  const [oldMappings, setOldMappings] = React.useState([]);
  const users = useSelector((store) => store.user.users);
  const mappings = useSelector((store) => store.devices.mappings);

  const [raspberryID, setRaspberryID] = React.useState("");

  const classes = useStyles();
  const dispatch = useDispatch();

  React.useEffect(() => {
    if (!didGet) {
      setDidGet(true);
      dispatch(loadMappings());
    }
    if (mappings.length !== oldMappings.length) {
      setShouldUpdate(!shouldUpdate);
      setOldMappings(mappings);
    }
  }, [
    dispatch,
    didGet,
    mappings.length,
    oldMappings.length,
    mappings,
    shouldUpdate,
  ]);

  const handleSubmit = () => {
    if (raspberryID !== "") {
      dispatch(addRaspberryToUser(selectedUser, raspberryID));
    }
    handleClose();
  };

  const handleRaspberryIDChange = (event) => {
    setRaspberryID(event.target.value);
  };

  const handleChange = (event) => {
    setSelectedUser(event.target.value);
  };

  const handleClose = () => {
    dispatch(closePopup());
  };

  const handleDelete = (item) => {
    dispatch(deleteMapping(item));
  };

  return (
    <div className={classes.popupbox}>
      <div className={classes.box}>
        <span className={classes.closeicon} onClick={handleClose}>
          x
        </span>
        <h2 className={classes.header}>Map User to Raspberry</h2>
        {shouldUpdate && <span></span>}
        {mappings &&
          mappings.map((item) => (
            <MappingsListItem
              item={item}
              user={users.find((e) => e.id === item.userId)}
              key={mappings.indexOf(item)}
              onDelete={handleDelete}
            />
          ))}
        <div>
          <FormControl className={classes.formControl}>
            <InputLabel id="label">User</InputLabel>
            <Select
              id="demo-simple-select-helper"
              value={selectedUser || ""}
              label="User"
              onChange={handleChange}
            >
              {users.map((user, ix) => (
                <MenuItem key={ix} value={user}>
                  <em>{user.email}</em>
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        </div>
        <TextField
          fullWidth
          label="Raspberry ID"
          name="raspberryID"
          variant="outlined"
          placeholder="Raspberry ID"
          value={raspberryID}
          style={{ marginBottom: "10px", marginTop: "10px" }}
          onChange={handleRaspberryIDChange}
        />
        <Button className={classes.button} onClick={handleSubmit}>
          Submit
        </Button>
      </div>
    </div>
  );
};
export default MapRaspberryPopup;

const MappingsListItem = ({ item, user, onDelete }) => {
  const classes = useStyles();

  const deleteMapping = () => {
    onDelete(item);
  };

  return (
    <div className={classes.boxik}>
      <div>
        {user && (
          <span className={classes.columns}>User Email: {user.email}</span>
        )}
        <span className={classes.columns}>User ID: {item.userId}</span>
        <span className={classes.columns}>
          Raspberry ID: {item.raspberryId}
        </span>
        <span className={classes.columns}>Session ID: {item.session_id}</span>
      </div>
      <Button className={classes.deleteButton} onClick={deleteMapping}>
        X
      </Button>
    </div>
  );
};
MappingsListItem.propTypes = {
  item: PropTypes.object,
  user: PropTypes.object,
  onDelete: PropTypes.func,
};
