import {
  DELETE_MAPPING,
  DELETE_MAPPING_FAILED,
  DELETE_MAPPING_SUCCESS,
  LOAD_DEVICES,
  LOAD_DEVICES_FAILED,
  LOAD_DEVICES_SUCCESS,
  LOAD_SESSION_LOGS,
  LOAD_SESSION_LOGS_FAILED,
  LOAD_SESSION_LOGS_SUCCESS,
  LOAD_MAPPINGS,
  LOAD_MAPPINGS_FAILED,
  LOAD_MAPPINGS_SUCCESS,
  ADD_RASP_TO_USER,
  ADD_RASP_TO_USER_SUCCESS,
  ADD_RASP_TO_USER_FAILED,
} from "../actionTypes/devices";
import { SHOW_SESSION_LOGS_POPUP } from "../actionTypes/popups";

export function deleteMapping(mapping) {
  return async (dispatch) => {
    dispatch({ type: DELETE_MAPPING });
    const url =
      "https://fm.ki.lt/api/v1/dataexchange/mappings?api_token=6c3ec4bbb801163e98fec95acf555193fea410ea21270ab5fd3c58f8c264fba1d1df794fb2ee97a24ab5d7de424e3010";
    try {
      const req = new Request(url, {
        method: "DELETE",
        body: JSON.stringify({ sessionId: mapping.session_id }),
        headers: {
          "Content-Type": "application/json",
        },
      });
      const response = await fetch(req).then();
      if (response.ok) {
        dispatch({ type: DELETE_MAPPING_SUCCESS, mapping });
      } else {
        dispatch({ type: DELETE_MAPPING_FAILED });
      }
    } catch (e) {
      dispatch({ type: DELETE_MAPPING_FAILED });
    }
  };
}

export function loadMappings() {
  return async (dispatch) => {
    dispatch({ type: LOAD_MAPPINGS });
    const url =
      "https://fm.ki.lt/api/v1/dataexchange/mappings?api_token=6c3ec4bbb801163e98fec95acf555193fea410ea21270ab5fd3c58f8c264fba1d1df794fb2ee97a24ab5d7de424e3010";
    try {
      const req = new Request(url, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
        },
      });
      const response = await fetch(req).then();
      if (response.ok) {
        const data = await response.json();
        dispatch({ type: LOAD_MAPPINGS_SUCCESS, mappings: data.mappings });
      } else {
        dispatch({ type: LOAD_MAPPINGS_FAILED });
      }
    } catch (e) {
      dispatch({ type: LOAD_MAPPINGS_FAILED });
    }
  };
}

export function loadDevices() {
  return async (dispatch) => {
    dispatch({ type: LOAD_DEVICES });
    const devicesUrl =
      "https://fm.ki.lt/api/v1/dataexchange/device?api_token=6c3ec4bbb801163e98fec95acf555193fea410ea21270ab5fd3c58f8c264fba1d1df794fb2ee97a24ab5d7de424e3010";
    try {
      const devicesReq = new Request(devicesUrl, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
        },
      });
      const devicesResponse = await fetch(devicesReq).then();
      if (devicesResponse.ok) {
        const data = await devicesResponse.json();
        dispatch({ type: LOAD_DEVICES_SUCCESS, devices: data.devices });
      } else {
        dispatch({ type: LOAD_DEVICES_FAILED });
      }
    } catch (e) {
      dispatch({ type: LOAD_DEVICES_FAILED });
    }
  };
}

export function loadSessionLogs(deviceId, startDate, endDate) {
  return async (dispatch) => {
    dispatch({ type: LOAD_SESSION_LOGS });
    const timeStamp = new Date().getTime();
    const yesterdayTimeStamp = timeStamp - 24 * 60 * 60 * 1000;
    const cDate = new Date(yesterdayTimeStamp).toJSON().slice(0, 10);
    const sessionStartDate =
      startDate === undefined
        ? cDate
        : new Date(startDate).toJSON().slice(0, 10);
    const sessionEndDate =
      endDate === undefined
        ? new Date().toJSON().slice(0, 10)
        : endDate.toJSON().slice(0, 10);
    const url =
      "https://fm.ki.lt/api/v1/dataexchange/data?api_token=6c3ec4bbb801163e98fec95acf555193fea410ea21270ab5fd3c58f8c264fba1d1df794fb2ee97a24ab5d7de424e3010&device_id=" +
      deviceId +
      "&start=" +
      sessionStartDate +
      "&end=" +
      sessionEndDate;
    try {
      const req = new Request(url, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
        },
      });
      const resp = await fetch(req).then();
      if (resp.ok) {
        const data = await resp.json();
        dispatch({ type: LOAD_SESSION_LOGS_SUCCESS, sessionData: data.data });
        if (startDate === undefined) {
          dispatch({ type: SHOW_SESSION_LOGS_POPUP });
        }
      } else {
        dispatch({ type: LOAD_SESSION_LOGS_FAILED });
      }
    } catch (e) {
      dispatch({ type: LOAD_SESSION_LOGS_FAILED });
    }
  };
}

export function addRaspberryToUser(user, rasp) {
  return async (dispatch) => {
    const url =
      "https://fm.ki.lt/api/v1/dataexchange/mappings?api_token=6c3ec4bbb801163e98fec95acf555193fea410ea21270ab5fd3c58f8c264fba1d1df794fb2ee97a24ab5d7de424e3010";
    dispatch({ type: ADD_RASP_TO_USER });
    try {
      const data = {
        userId: user.id,
        raspberryId: rasp,
      };
      const req = new Request(url, {
        method: "POST",
        body: JSON.stringify(data),
        headers: {
          "Content-Type": "application/json",
        },
      });
      const response = await fetch(req);
      if (response.ok) {
        dispatch({ type: ADD_RASP_TO_USER_SUCCESS });
      } else {
        dispatch({ type: ADD_RASP_TO_USER_FAILED });
      }
    } catch (e) {
      dispatch({ type: ADD_RASP_TO_USER_FAILED });
    }
  };
}
