import { useDispatch, useSelector } from "react-redux";
import React from "react";
import { makeStyles, Button } from "@material-ui/core";
import PropTypes from "prop-types";
import DatePicker from "react-datepicker";

import "react-datepicker/dist/react-datepicker.css";
import { closePopup, showDevicesPopup } from "../../state/actions/popups";
import { loadSessionLogs } from "../../state/actions/devices";

const useStyles = makeStyles({
  popupbox: {
    position: "absolute",
    background: "#00000050",
    width: "100%",
    height: "100vh",
    top: 0,
    left: 0,
  },
  backButton: {
    height: "35px",
    color: "white",
    background: "black",
    fontSize: "1em",
    textTransform: "none",
    borderRadius: "5px",
    borderColor: "#FFFFFF",
    borderWidth: "1px",
    margin: "0px auto 5px auto",
    borderStyle: "solid",
    "&:hover": {
      color: "black",
      background: "white",
      borderColor: "black",
    },
  },
  box: {
    minHeight: "500px",
    position: "relative",
    width: "70%",
    margin: "0 auto",
    height: "auto",
    maxHeight: "70vh",
    marginTop: "calc(15vh - 20px)",
    background: "white",
    borderRadius: "4px",
    padding: "20px",
    border: "1px solid black",
    overflow: "auto",
  },
  closeicon: {
    content: "x",
    cursor: "pointer",
    position: "fixed",
    right: "calc(15% - 12.5px)",
    top: "calc(15vh - 32.5px)",
    background: "white",
    width: "25px",
    height: "25px",
    borderRadius: "50%",
    lineHeight: "20px",
    textAlign: "center",
    border: "1px solid black",
    fontSize: "20px",
    fontWeight: "bold",
  },
  button: {
    border: "1px solid black",
    background: "green",
    color: "white",
    marginTop: "10px",
  },
  formControl: {
    display: "flex",
    marginBottom: "10px",
    minWidth: 200,
  },
  boxik: {
    color: "black",
    background: "white",
    fontSize: "1em",
    whiteSpace: "wrap",
    textTransform: "none",
    borderRadius: "5px",
    borderColor: "black",
    borderWidth: "1px",
    margin: "10px 10px",
    borderStyle: "solid",
    padding: "5px",
  },
  picker: {
    fontSize: "20px",
    marginBottom: "5px",
  },
  header: {
    margin: "10px 0px",
  },
});

const SessionLogsPopup = () => {
  const [startDate, setStartDate] = React.useState(
    new Date(new Date().getTime() - 24 * 60 * 60 * 1000)
  );
  const [endDate, setEndDate] = React.useState(new Date());
  const sessionData = useSelector((store) => store.devices.sessionData);

  const classes = useStyles();
  const dispatch = useDispatch();

  const handleClose = () => {
    dispatch(closePopup());
  };

  const goBack = () => {
    dispatch(showDevicesPopup());
  };

  const loadLogs = (date) => {
    setStartDate(date);
    if (sessionData !== undefined && date < endDate) {
      dispatch(loadSessionLogs(sessionData.device_id, date, endDate));
    }
  };

  const endDateSelected = (date) => {
    setEndDate(date);
    if (sessionData !== undefined && startDate < date) {
      dispatch(loadSessionLogs(sessionData.device_id, startDate, date));
    }
  };

  return (
    <div className={classes.popupbox}>
      <div className={classes.box}>
        <span className={classes.closeicon} onClick={handleClose}>
          x
        </span>
        <Button className={classes.backButton} onClick={goBack}>
          GO BACK
        </Button>
        <h2 className={classes.header}>Session logs</h2>
        <div>
          Pick Start Date:
          <DatePicker
            className={classes.picker}
            selected={startDate}
            onChange={loadLogs}
          />
        </div>
        <div>
          Pick End Date:
          <DatePicker
            className={classes.picker}
            selected={endDate}
            onChange={endDateSelected}
          />
        </div>
        {sessionData && (
          <div>
            <div>Device ID: {sessionData.device_id}</div>
            <div>Start: {sessionData.period.start}</div>
            <div>End: {sessionData.period.end}</div>
          </div>
        )}

        {sessionData &&
          sessionData.sessions.map((item) => (
            <SessionLogsPopupItem
              item={item}
              key={sessionData.sessions.indexOf(item)}
            />
          ))}
      </div>
    </div>
  );
};
export default SessionLogsPopup;
const SessionLogsPopupItem = ({ item }) => {
  return (
    <div>
      {item && (
        <div>
          <div>Session ID: {item.session_id}</div>
          <div>User ID: {item.user_id}</div>
        </div>
      )}
      {item &&
        item.data.map((dataItem) => (
          <SessionLogsPopupDataItem
            dataItem={dataItem}
            key={item.data.indexOf(dataItem)}
          />
        ))}
    </div>
  );
};
SessionLogsPopupItem.propTypes = {
  item: PropTypes.object,
};
const SessionLogsPopupDataItem = ({ dataItem }) => {
  const classes = useStyles();

  return (
    <div className={classes.boxik}>
      <div>Table ID: {dataItem.tableId}</div>
      <div>Cells Row ID: {dataItem.cellsRowId}</div>
      <div>Row Index: {dataItem.rowIndex}</div>
      <div>Column Index: {dataItem.columnIndex}</div>
      <div>Created at: {dataItem.created_at}</div>
    </div>
  );
};
SessionLogsPopupDataItem.propTypes = {
  dataItem: PropTypes.object,
};
