import {
  SIGN_IN,
  SIGN_IN_SUCCESS,
  SIGN_IN_FAILED,
  GET_USERS,
  GET_USERS_SUCCESS,
  GET_USERS_FAILED,
  ADD_TABLE_TO_USER,
  ADD_TABLE_TO_USER_SUCCESS,
  ADD_TABLE_TO_USER_FAILED,
} from "../actionTypes/user";
import { auth, db } from "../../firebase";

export function signIn(user) {
  return async (dispatch) => {
    dispatch({ type: SIGN_IN });
    try {
      auth
        .signInWithEmailAndPassword(user.email, user.password)
        .then((value) => {
          db.collection("users")
            .doc(value.user.uid)
            .get()
            .then((snapshot) => {
              const dbUser = snapshot.data();
              if (dbUser.role === "admin") {
                dispatch({ type: SIGN_IN_SUCCESS, user: dbUser });
              } else {
                dispatch({ type: SIGN_IN_FAILED });
              }
            });
        });
    } catch (e) {
      dispatch({ type: SIGN_IN_FAILED });
    }
  };
}

export function getUsers() {
  return async (dispatch) => {
    dispatch({ type: GET_USERS });
    try {
      db.collection("users")
        .get()
        .then((snapshot) => {
          const data = [];
          snapshot.forEach((snap) => {
            data.push({ ...snap.data(), id: snap.id });
          });
          dispatch({ type: GET_USERS_SUCCESS, data });
        });
    } catch (e) {
      dispatch({ type: GET_USERS_FAILED });
    }
  };
}

export function addTableToUser(user, table) {
  return async (dispatch) => {
    dispatch({ type: ADD_TABLE_TO_USER });
    try {
      const ref = db.collection("users").doc(user.id).collection("tables");
      ref.get().then((snapshots) => {
        ref.add({ tableId: table.id, tableOrder: snapshots.size });
        dispatch({ type: ADD_TABLE_TO_USER_SUCCESS });
      });
    } catch (e) {
      dispatch({ type: ADD_TABLE_TO_USER_FAILED });
    }
  };
}
