import { useDispatch, useSelector } from "react-redux";
import React from "react";
import { makeStyles } from "@material-ui/core";
import { useEffect } from "react";
import PropTypes from "prop-types";

import { closePopup } from "../../state/actions/popups";
import { loadDevices, loadSessionLogs } from "../../state/actions/devices";

const useStyles = makeStyles({
  popupbox: {
    position: "absolute",
    background: "#00000050",
    width: "100%",
    height: "100vh",
    top: 0,
    left: 0,
  },
  box: {
    position: "relative",
    width: "70%",
    margin: "0 auto",
    height: "auto",
    maxHeight: "70vh",
    marginTop: "calc(15vh - 20px)",
    background: "white",
    borderRadius: "4px",
    padding: "20px",
    border: "1px solid black",
    overflow: "auto",
  },
  closeicon: {
    content: "x",
    cursor: "pointer",
    position: "fixed",
    right: "calc(15% - 12.5px)",
    top: "calc(15vh - 32.5px)",
    background: "white",
    width: "25px",
    height: "25px",
    borderRadius: "50%",
    lineHeight: "20px",
    textAlign: "center",
    border: "1px solid black",
    fontSize: "20px",
    fontWeight: "bold",
  },
  button: {
    border: "1px solid black",
    background: "green",
    color: "white",
    marginTop: "10px",
  },
  formControl: {
    display: "flex",
    marginBottom: "10px",
    minWidth: 200,
  },
  boxik: {
    color: "black",
    background: "white",
    fontSize: "1em",
    whiteSpace: "wrap",
    textTransform: "none",
    borderRadius: "5px",
    borderColor: "black",
    borderWidth: "1px",
    margin: "10px 10px",
    borderStyle: "solid",
    padding: "5px",
    "&:hover": {
      color: "white",
      background: "black",
      borderColor: "black",
      cursor: "pointer",
    },
  },
  circle: {
    marginLeft: "5px",
    width: "25px",
    height: "25px",
    webkitBorderRadius: "15px",
    mozBorderradius: "15px",
    borderRadius: "15px",
  },
  connectionText: {
    verticalAlign: "center",
    marginTop: "auto",
    marginBottom: "auto",
  },
  header: {
    margin: "10px 0px",
  },
});

const DevicesPopup = () => {
  const devices = useSelector((store) => store.devices.devices);

  const classes = useStyles();
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(loadDevices());
  }, [dispatch]);

  const handleClose = () => {
    dispatch(closePopup());
  };

  return (
    <div className={classes.popupbox}>
      <div className={classes.box}>
        <span className={classes.closeicon} onClick={handleClose}>
          x
        </span>
        <h2 className={classes.header}>Active sessions</h2>
        {devices &&
          devices.map((item) => (
            <DevicePopupItem item={item} key={devices.indexOf(item)} />
          ))}
      </div>
    </div>
  );
};
export default DevicesPopup;
const DevicePopupItem = ({ item }) => {
  const classes = useStyles();
  const dispatch = useDispatch();

  const openLogs = () => {
    dispatch(loadSessionLogs(item.device_id));
  };

  return (
    <div className={classes.boxik} onClick={openLogs}>
      <div>Device ID: {item.device_id}</div>
      <div>Host name: {item.device_host_name}</div>
      <div>Device IP address: {item.device_ip_address}</div>
      <div>
        Session ID:{" "}
        {item.current_session_id === "" ? "N/A" : item.current_session_id}
      </div>
      <div>
        User ID: {item.current_user_id === "" ? "N/A" : item.current_user_id}
      </div>
      <div
        style={{
          display: "flex",
        }}
      >
        <span className={classes.connectionText}>Is Raspberry Connected:</span>
        <div
          className={classes.circle}
          style={{
            background: item.is_connected ? "green" : "red",
          }}
        ></div>
      </div>
      <div>Last connection time: {item.last_connection_time}</div>
      <div>Update at: {item.updated_at}</div>
    </div>
  );
};
DevicePopupItem.propTypes = {
  item: PropTypes.object,
};
